
.fondo_login{
 
 
  grid-column: 1 / 11;
  grid-row: #{$inicio}/ #{$inicio+$rows};
  min-height: 50vh;
  
  background-color: color(claro);
    
  @media screen and (min-width:$cel){
   
  
    
  
  }
  @media screen and (min-width:$tablet){
   
   
    
 
  }
  @media screen and (min-width:$laptop){
       
    grid-column: 1 / 13;
  
  }
}
.perfil_login{
  grid-column:2 / 10;
  grid-row: #{($inicio) + 1 }/ #{($inicio + $rows) - 1};
  min-height: 400px;
  height: calc((100vh - 45px)* 9 / #{$rows});



  display: grid;
  grid-auto-flow: dense;
  grid-template-columns: repeat(1,1fr);
  //grid-template-rows: calc((100vh - 45px)* 9 / #{$rows} / 2);
  grid-template-rows: 20% 30% 10% 40%;
  background-color: $fondo-caja;
  justify-items: center;
  align-items: top;
  box-shadow: 2px 3px 10px $border-caja, -1px -1px 10px $border-caja;
  @media screen and (min-width:$cel){
    background-color: $fondo-caja;
  }
  @media screen and (min-width:$tablet){
  
  min-height: 580px;
  height: calc((100vh - 45px)* 5 / #{$rows});
  grid-template-rows:  30% 70%;
  grid-template-columns: repeat(2,1fr);

  }
  @media screen and (min-width : $laptop){
    
    grid-column: 2 / 12;
    background-color: $fondo-caja;
  }
}



.ingreso
{
  
 
display: GRID;
 
  grid-template-columns: repeat(1,1fr);
grid-template-rows: repeat(4,7%);
justify-content: center;
align-content: top;
grid-row-gap: 15px;

& h2{
align-self: start;
}
&-item{
  text-align: center;
height: 25px;
border: none;
border-bottom: 1px solid color(2);
background-color: color(claro)

}
& .message{
  text-align: center;
  height: 25px;
 
  color: color(3)
}

@media screen and (min-width:$tablet){
  align-content: top;
  order: 2;
  }
}.messageError{
  text-align: center;
  height: 25px;
 
  color: red
}

.registro
{


display: GRID;
grid-template-columns: repeat(1,1fr);
grid-template-rows: repeat(6,7%);
justify-content: center;
align-content: top;
grid-row-gap: 15px;
text-align: center;
&-item{
  text-align: center;
height: 25px;
border: none;
border-bottom: 1px solid color(2);
background-color: color(claro)
}

@media screen and (min-width:$tablet){
  
  order: 3;
  }
}

.tittle-login{
align-self: center;

}

.registro-item:focus,.ingreso-item:focus{
  border-bottom: 1px solid color(1);

  outline: none;
}


